.CV{
    height: 230mm;
    width: 168mm;
    font-family:Arial, Helvetica, sans-serif;
}

h1,h2,p,span{
    color:black;
}


/********************************************/




.CVGen h1{
    font-size: 3.052rem;
    font-family:'Times New Roman', Times, serif;
}

.CVGen p{
    margin: 10px 0 10px 0;
    font-size: 1 rem;
}

.CVGen, .CVEdu, .CVExp{
    margin:10px;
    padding-bottom: 10px;
    border-bottom: dashed black;
}




/********************************************/
.sectionHeader{
    font-size:2.441rem;
    font-family:'Times New Roman', Times, serif;
}


/********************************************/



.educationList li{
display: flex;
flex-direction: row;
justify-content: space-between;
margin:20px;
}



.educationLi h2{
    font-weight:bold;
    font-size:1.563rem;
}

.educationDate h2{
    font-size:1.25rem;
}

/********************************************/


.experienceList li{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin:20px;
    }
    

.experienceList li h2{
    font-size:1.25 rem;
}
.experienceList li h2 span{
    font-weight:bolder;
    font-size:1.563rem;
}
/********************************************/



